body.login {
  background: #738178; }

body.login div#login h1 a {
  width: 218px;
  background-image: url(../images/default.png);
  -webkit-background-size: 200px 200px;
          background-size: 200px; }

body.login div#login form#loginform {
  background: #738178;
  border: 1px solid #112110; }

body.login div#login form#loginform p {
  color: #112110; }

body.login div#login form#loginform p label {
  color: #112110; }

body.login div#login form#loginform input {
  width: 100%;
  color: white;
  padding: 3px 5px;
  resize: none;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 1.4;
  border: 1px solid #112110;
  background: #738178; }

body.login div#login form#loginform p.forgetmenot {
  margin: 10px 0; }

body.login div#login form#loginform p.forgetmenot input#rememberme {
  width: auto;
  margin-right: 10px;
  float: left; }

body.login div#login form#loginform p.submit input#wp-submit {
  background: #3d9650;
  border: 1px solid #3d9650;
  border-bottom: 1px solid #3d9650;
  border-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  padding: 0.85em 1em;
  margin: 0 0 1rem 0;
  font-size: 0.9rem;
  height: auto; }

body.login div#login p#nav a {
  color: #112110; }

body.login div#login p#backtoblog a {
  color: #112110; }

#lostpasswordform {
  background: #738178;
  border: 1px solid #112110; }

#lostpasswordform label {
  color: #112110; }

#lostpasswordform input {
  width: 100%;
  color: white;
  padding: 3px 5px;
  resize: none;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 1.4;
  border: 1px solid #112110;
  background: #738178; }

#lostpasswordform p.submit {
  margin-top: 20px !important; }

#lostpasswordform input#wp-submit {
  margin-top: 20px;
  float: left;
  background: #3d9650;
  border: 1px solid #3d9650;
  border-bottom: 1px solid #3d9650;
  border-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  padding: 0.85em 1em;
  margin: 0 0 1rem 0;
  font-size: 0.9rem;
  height: auto; }
