// _actions.scss
.tzu-build {
	.actions {

	    box-shadow: inset 0 0 100px rgba(0,0,0,.5);
	    background: #738178;
	    /* background: #71b47f; */
	    float: left;
	    width: 100%;
	    position: fixed;
	    top: 0px;
	    padding-top: 5px;
	    padding-bottom: 5px;
	    border-bottom: #eaeeea;
	    border-bottom: 5px solid 
	}
}

.well {	
	background:$ecowelle-mud;
	padding:$default-box-padding;
	ul {
		list-style: none;
		margin:0;
		padding:0;
	}
	ul li a {
		color:#e3ece7;
	}
	ul li a:hover {
		text-decoration: underline;
	}
	.row .columns {
		text-align: center;
		&.small-6.medium-3 {
		}
	}
}

 