

.system-header {
box-shadow: inset 0 0 100px rgba(0,0,0,.5);
    /*background:#738178;*/
    /*background:#71b47f;*/
    /*background:#a37159;*/
    float: left;
    width:100%;
    position: fixed;
    top:0px;
    padding-top:5px;
    padding-bottom:5px;
    border-bottom:#eaeeea;
    border-bottom: 5px solid #eaeeea;
    z-index:500;
}

.system-header button.logo {
    height:45px;
    width:45px;
    display:block;
    font-size: 0;
    background:url('../images/ecowelle-logo-utilities.png');
    background:url('../images/default.png');
    background-size:45px 45px;
    float:left;
}
@media print, screen and (max-width: 64em) {
    .system-header button.logo {
        height:20px;
        width:111px;
        background-size:111px 20px;
        margin-top: 14px;
    }
}

.tzu-loader {
    background: url('.images/39.gif');
    height:64px;
    width:64px;
}
.system-header ul {
    list-style: none;
    padding:0;
    margin:0;
    /*background:red;*/
    float: right;
}
.system-header ul li {
    float:left;
    /*background:blue;*/
    display: block;
    margin-left:10px;
}
.system-header ul li a {
    float:left;
    display: block;
    font-size: 35px;
    line-height: 45px;
    width:45px;
    padding:0px;
    box-sizing: border-box;
    height:45px;
    color:#543348;
}
.system-header ul li a .fa{
    padding-top:0;
    margin-top:3px;
    font-size: 27px;

}

.system-header ul li a.active {
    color:rgba(217, 225, 236, .8);
}