.report-view {
	padding:15px 15px 100px 15px;
	position:relative;
}

@media print, screen and (min-width: 64em) {
	.report-view {

		padding: 15px 30px 100px 30px;
	}
}


.report-controls {
	// height:100px;
	position:fixed;
	bottom:0;
	right:0;
	left:0;
	> .row {
		margin:0 auto;
		max-width:62.5rem !important;
		> .columns {
				background:#738178;
				    background: white;
    border-top: 4px solid #a37159;
		}
	}

}


/*/////////////////////////*/
.t1 {
    background: #738178;
    color: #e3ece7;
    width: 100%;
    padding: 3px !important;
    font-size: 10px;
    margin: 0;
    font-weight: normal;
    text-align: center;
    text-transform: uppercase;
} 

.tzu-checklist {
	margin-bottom:10px;
}
.tzu-checklist h4 ,
.tzu-checklist h5 ,
.tzu-checklist p {
	display: block;
	float: left;
	/*background:gold;*/
	width:100%;
	border-bottom:1px solid black;
	margin:0;
	word-wrap: break-word;
	    font-size: 10px;
}
.tzu-checklist h4 {
	font-size: 12px;
	padding:7px !important;
	text-transform:uppercase;
}
.tzu-checklist h5 {
	font-size: 11px;
	padding:7px;
}
.tzu-checklist p {
	font-size: 10px;
	padding:7px;
}
.tzu-checklist h4 span:first-of-type,
.tzu-checklist h5 span:first-of-type,
.tzu-checklist p span:first-of-type{
	display: block;
	float: left;
	/*background:green;*/
	border-right:1px solid black;
	font-weight: bold;
	width:50%;
}
.tzu-checklist h4 span:last-of-type,
.tzu-checklist h5 span:last-of-type,
.tzu-checklist p span:last-of-type{
	display: block;
	float: left;
	/*background:red;*/
	width:50%;
	    padding-left: 7px;
    box-sizing: border-box;
}

