

.ecowelle-field input[type="text"], 
.ecowelle-field input[type="password"], 
.ecowelle-field input[type="number"], 
.ecowelle-field input[type="search"], 
.ecowelle-field input[type="email"], 
.ecowelle-field input[type="url"], 
.ecowelle-field textarea, 
.ecowelle-field select {
    width: 100%;
    resize: none;
    box-sizing: border-box;
    font-size: 14px;
    line-height: 1.4;
    padding: 3px 5px;
    margin: 0px;
}
.ecowelle-form [type="text"], 
.ecowelle-form [type="password"], 
.ecowelle-form [type="date"], 
.ecowelle-form [type="datetime"], 
.ecowelle-form [type="datetime-local"], 
.ecowelle-form [type="month"], 
.ecowelle-form [type="week"], 
.ecowelle-form [type="email"], 
.ecowelle-form [type="number"], 
.ecowelle-form [type="search"], 
.ecowelle-form [type="tel"], 
.ecowelle-form [type="time"], 
.ecowelle-form [type="url"], 
.ecowelle-form [type="color"], 
.ecowelle-form textarea {
    // background-color: rgb(113, 180, 127);
    // color: rgb(227, 232, 228);
    color: black;
    display: block;
    box-sizing: border-box;
    width: 100%;
    height: 2.4375rem;
    font-family: inherit;
    font-size: 1rem;
    box-shadow: rgba(10, 10, 10, 0.1) 0px 1px 2px inset;
    -webkit-appearance: none;
    border:1px solid #738178;
    padding: 0.5rem;
    background:whitesmoke;
    margin: 0px 0px 1rem;
    border-radius: 0px;
    transition: box-shadow 0.5s, border-color 0.25s ease-in-out, -webkit-box-shadow 0.5s;
}

input[type="button"].form-submit,
button.form-submit, 
.ecowelle-form button {
    width: 100%;
    color: rgb(17, 33, 16);
    text-transform: uppercase;
    font-weight: bold;
    background: rgb(227, 232, 228);
}
[data-whatinput="mouse"] .button {
    outline: 0px;
}
button, 
html input[type="button"], 
input[type="reset"], 
input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer;
}


.ecowelle-form {
    padding-bottom:60px;
	.form-section {
    	border-image: initial;
    	padding: 1rem;
    	transition: all 0.25s ease-out, -webkit-box-shadow 0.5s;
	}
}
@media print, screen and (min-width: 64em) {
    .ecowelle-form {
		.form-section {
    			border: 1px solid #738178;
    			padding: 1rem;
		}
	}
}

.magikform h3 span {
	font-weight:bold;
}


.ng-invalid {
    background: rgba(229, 58, 58, 0.3);
}