// button.scss
.tzu-build {
	a.button,
	.button {
		display: block;
		background:$button-background;
		color:$button-color;
		//border:1px solid grey;
		position:relative;
		margin-bottom:0;
		border-bottom:3px solid green;

		    background: white;
    border: 1px solid black;
    text-transform: uppercase;
    color: black;

		&::after {
			opacity:0;
			-ms-transform: scale(0.5, 0.5); /* IE 9 */
		    -webkit-transform: scale(0.5, 0.5); /* Safari */
		   	transform: scale(0.5, 0.5);
		 	transition: all 0.6s ease;

			background-color:$button-background;
			background-image:url($loading-gif);
			content: "";
			display:block;
			position:absolute;
			z-index:300;
			left:0;
			top:0;
			width:100%;
			height:100%;
			
		}
		&.loading::after {
		// &.loading::before {
		 	opacity:1;
			-ms-transform: scale(1, 1); /* IE 9 */
	    	-webkit-transform: scale(1, 1); /* Safari */
   			transform: scale(1, 1);
		}
	}
	input.save-form {
		width:100%;
		text-transform:uppercase;
		font-size:24px;
		font-weight:100;
		text-align:center;
		border:0px solid white;
		border-bottom:3px solid green;
		    width: 100%;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 100;
    text-align: center;
    border: 0px solid white;
    border: 1px solid black;
    background: white;
    margin-top: 20px;
    color: black;
	}
}