.tzu-build a.button,
.tzu-build .button {
  display: block;
  background: #71b47f;
  color: #d9e1d8;
  position: relative;
  margin-bottom: 0;
  border-bottom: 3px solid green;
  background: white;
  border: 1px solid black;
  text-transform: uppercase;
  color: black; }
  .tzu-build a.button::after,
  .tzu-build .button::after {
    opacity: 0;
    -ms-transform: scale(0.5, 0.5);
    /* IE 9 */
    -webkit-transform: scale(0.5, 0.5);
    /* Safari */
    transform: scale(0.5, 0.5);
    transition: all 0.6s ease;
    background-color: #71b47f;
    background-image: url("../images/39.gif");
    content: "";
    display: block;
    position: absolute;
    z-index: 300;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%; }
  .tzu-build a.button.loading::after,
  .tzu-build .button.loading::after {
    opacity: 1;
    -ms-transform: scale(1, 1);
    /* IE 9 */
    -webkit-transform: scale(1, 1);
    /* Safari */
    transform: scale(1, 1); }

