	// body.has-modal {
		// overflow: hidden;
	// }
.tzu-build {
	.tzu-modal-background {
        /* modal container fixed across whole screen */
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        /* z-index must be higher than .modal-background */
        z-index: 1000;
        background: rgba(255,255,255,.75);
        /* enables scrolling for tall modals */
        overflow: auto;
		padding:20px;
		display:none;
	    .tzu-modal {
	    	padding: 20px;
	    	// box-shadow: 5px 6px 0px 0px green;
    		border: 3px solid #738178;
    		position:relative;
			background:white;
			width:100%;
			max-width:400px;
	    	min-height: 400px;
	    	margin:60px auto 0;
			opacity:.4;
			-ms-transform: scale(0.5, 0.5); /* IE 9 */
		    -webkit-transform: scale(0.5, 0.5); /* Safari */
		   	transform: scale(0.2, 0.5);
		 	transition: all 0.8s ease;
		 	&.loading {
				opacity:1;
				-ms-transform: scale(1, 1);
	    		-webkit-transform: scale(1, 1);
	   			transform: scale(1, 1);
			}
	    }
 		&.loading {
 			display:block;
	    }

	    button {
			i {
			}
		}
		.tzu-modal-header {
			border-bottom:1px solid black;
			width: 100%;
			float:left;
			height:100px;
		}
		.tzu-modal-footer {
			background:#738178;
			float:left;
			width: 100%;
			position:absolute;
			bottom:0;
			position: absolute;
    		bottom: 0;
    		left: 0;
    		height: 30px;
    		.button {
    		    width: 80%;
    			margin-top: -10px;
    			margin: -10px 10% 0;
    		}
		}
		.tzu-modal-content {
			float:left;
			width: 100%;
		}
    }
}