}
.page-view {
	background:#738178;
	background:#a39a96;
	min-height:100vh;
	height:auto;
}

.system-layout {
	background:whitesmoke;
	//padding-top: 80px;
	padding-top: 60px;
    min-height: 100vh;
    height:100%;
}   

@media print, screen and (max-width: 64em) {
	.collapse-padding-for-mobile {
		padding-right:0 !important;
		padding-left:0 !important;
	}
}
.view-header {
	padding:15px;
	background:#91aa92;
	background:#a39a96;
}
.content-box {
	padding:15px;
}
.view-title {
	margin:0;
	padding:0;
	font-size:18px;
	font-weight:700;
	text-transform: uppercase;
    font-weight: 100;
    text-align:center;
}


.fuzzy-filter {}
ul.mixed-table {
	list-style:none;
	margin:0;
	padding:0;
	li {
		padding:7px;
		&:nth-child(odd) {
			background:#dce4dc;
			background:#eae8e8;
		}
		&:nth-child(even) {
			background:#71b47f;
			background:#fff;
		}
		&.titles {
			padding:14px 0;
			background:white;
		}
		.row {
			margin:0;	
		}
		h5 {
			margin:0;
			font-size:14px;
			text-transform:uppercase;
		}
		h6 {
			margin:0;
			font-size:14px;
			font-weight: 400;
			span {
				font-weight:800;
			}
			
		}
	}
}
@media print, screen and (max-width: 64em) {
	ul.mixed-table li h6 a {
		width:100%;
		padding:10px;
		border:1px solid grey;
		float:left;
	}
}


.task-group {
	text-transform:uppercase;
	background:#dce4dc;
	background:#c3b8b3;

	ul {
		padding:7px;
		margin:0;
		list-style:none;
		li {
			margin-bottom:10px;
		}
		li > a {
		    background: #71b47f;
		    background:white;
		    width: 100%;
		    display: block;
		    padding: 7px;
		    font-size: 18px;
		    border: 1px solid black;
    color: black;
		} 
	}
}

.task-group-header {
	padding:7px !important;
	background:white;
	width:100%;
	margin:0;
	text-transform:uppercase;
}

.control-bar{
	background:#738178;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    padding:5px 0;
}